import React, { useState } from "react";
import "./Calculator.css";
import Wrapper from "./components/Wrapper";
import Screen from "./components/Screen";
import ButtonBox from "./components/ButtonBox";
import Button from "./components/Button";

const buttonList = [
  ["C", "+-", "%", "/"],
  [7, 8, 9, "X"],
  [4, 5, 6, "-"],
  [1, 2, 3, "+"],
  [0, ".", "="],
];

const toLocaleString = (num) =>
  String(num).replace(/(?<!\..*)(\d)(?=(?:\d{3})+(?:\.|$))/g, "$1 ");

const removeSpaces = (num) => num.toString().replace(/\s/g, "");

const Calculator = () => {
  let [calc, setCalc] = useState({
    sign: "",
    num: 0,
    result: 0,
  });

  function numHandler(e) {
    e.preventDefault();
    const value = e.target.innerHTML;

    if (removeSpaces(calc.num).length < 16) {
      setCalc({
        ...calc,
        num:
          calc.num === 0 && value === "0"
            ? "0"
            : removeSpaces(calc.num) % 1 === 0
            ? toLocaleString(Number(removeSpaces(calc.num + value)))
            : toLocaleString(calc.num + value),
        result: !calc.sign ? 0 : calc.result,
      });
    }
  }

  function commaHandler(e) {
    e.preventDefault();
    const value = e.target.innerHTML;

    setCalc({
      ...calc,
      num: !calc.num.toString().includes(".") ? calc.num + value : calc.num,
    });
  }

  function signHandler(e) {
    setCalc({
      ...calc,
      sign: e.target.innerHTML,
      result: !calc.result && calc.num ? calc.num : calc.result,
      num: 0,
    });
  }

  function equalsHandler() {
    if (calc.sign && calc.num) {
      const math = (a, b, sign) =>
        sign === "+"
          ? a + b
          : sign === "-"
          ? a - b
          : sign === "X"
          ? a * b
          : a / b;

      setCalc({
        ...calc,
        result:
          calc.num === "0" && calc.sign === "/"
            ? "Can't divide with 0"
            : toLocaleString(
                math(
                  Number(removeSpaces(calc.result)),
                  Number(removeSpaces(calc.num)),
                  calc.sign
                )
              ),
        sign: "",
        num: 0,
      });
    }
  }

  function invertHandler() {
    setCalc({
      ...calc,
      num: calc.num ? toLocaleString(removeSpaces(calc.num) * -1) : 0,
      result: calc.result ? toLocaleString(removeSpaces(calc.result) * -1) : 0,
      sign: "",
    });
  }

  function percentHandler() {
    let num = calc.num ? parseFloat(removeSpaces(calc.num)) : 0;
    let result = calc.result ? parseFloat(removeSpaces(calc.result)) : 0;
    setCalc({
      ...calc,
      num: (num /= Math.pow(100, 1)),
      result: (result /= Math.pow(100, 1)),
      sign: "",
    });
  }

  function resetHandler() {
    setCalc({
      ...calc,
      sign: "",
      num: 0,
      result: 0,
    });
  }

  return (
    <article className="container">
      <h1 className="title">Калькулятор</h1>
      <Wrapper>
        <Screen value={calc.num ? calc.num : calc.result} />
        <ButtonBox>
          {buttonList.flat().map((btn, i) => {
            return (
              <Button
                key={i}
                className={btn === "=" ? "button_equals" : "button"}
                value={btn}
                onClick={
                  btn === "C"
                    ? resetHandler
                    : btn === "+-"
                    ? invertHandler
                    : btn === "%"
                    ? percentHandler
                    : btn === "="
                    ? equalsHandler
                    : btn === "/" || btn === "X" || btn === "-" || btn === "+"
                    ? signHandler
                    : btn === "."
                    ? commaHandler
                    : numHandler
                }
              />
            );
          })}
        </ButtonBox>
      </Wrapper>
    </article>
  );
};

export default Calculator;
